html {
  margin: 0;
  padding: 0;
}
ul {
  text-decoration: none;
  list-style-type: none;
  float: right;
}
li {
  display: inline;
  padding-right: 10px;
  cursor: pointer;
}
a:not(#about) {
  color: #ffffff;
}
a:visited {
  color: #fff;
}
a:active{
  color: #fff;
}
.App-header {
  font-size: calc(10px + 2vmin);
  background-color: grey;
  color: #ffffff;
  display: flex;
  justify-content: space-between;
  position: sticky;
  top: 0;
  position: -webkit-sticky;
}
h1 {
  margin-block-start: 0;
  margin-block-end: 0;
  float: left;
}
section {
  display: flex;
  justify-content: center;
  margin-top:40px;
  align-items: center;
}
#about {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
section div {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 800px;
  align-items: center;
  text-align: left;
}
footer {
  background-color:darkblue;
  color: #fff;
  height: 50px;
  padding:10px 5px;
}